import React, { Component } from "react";
import CourseDataService from "../course.service";
import { Link } from "react-router-dom";

export default class CourseList extends Component {
    constructor(props) {
        super(props);
        this.onChangeSearch = this.onChangeSearch.bind(this);
        this.retrieveCourses = this.retrieveCourses.bind(this);
        this.refreshList = this.refreshList.bind(this);
        this.setActiveCourse = this.setActiveCourse.bind(this);
        this.removeAll = this.removeAll.bind(this);
        this.searchCourse = this.searchCourse.bind(this);

        this.state = {
            courses: [],
            currentCourse: null,
            currentIndex: -1,
            search: ""
        };
    }

    componentDidMount() {
        this.retrieveCourses();
    }

    onChangeSearch(e) {
        const search = e.target.value;

        this.setState({
            search: search
        });
    }

    retrieveCourses() {
        CourseDataService.getAll() 
            .then(response => {
                this.setState({
                    courses: response.data
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    refreshList() {
        this.retrieveTutorials();
        this.setState({
            currentTutorial: null,
            currentIndex: -1
        });
    }

    setActiveCourse(course, index) {
        this.setState({
            currentCourse: course,
            currentIndex: index
        });
    }

    removeAll() {
        CourseDataService.deleteAll()
            .then(response => {
                console.log(response.data);
                this.refreshList();
            })
            .catch(e => {
                console.log(e);
            });
    }

    searchCourse() {
        CourseDataService.findOne(this.state.search)
            .then(response => {
                this.setState({
                    courses: response.data
                });
                console.log(response.data);
            })
            .catch(e => {
                console.log(e);
            });
    }

    render() {
        const { search, courses, currentCourse, currentIndex } = this.state;

        return (
            <div className="list row">
                <div className="col-md-8">
                    <div className="input-group mb-3">
                        <input  
                            type="text"
                            className="form-control"
                            placeholder="Search"
                            value={search}
                            onChange={this.onChangeSearch}
                        />
                        <div className="input-group-append">
                            <button 
                                className="btn btn-outline-secondary"
                                type="button"
                                onClick={this.searchCourse}
                            >Search</button>
                        </div>
                    </div>
                </div>
                <div className="col-md-6">
                    <h4>Courses List</h4>

                    <ul className="list-group">
                        {courses &&
                            courses.map((course, index) => (
                                <li
                                    className={
                                        "list-group-item" +
                                        (index === currentIndex ? "active" : "")
                                    }
                                    onClick={() => this.setActiveCourse(course, index)}
                                    key={index}
                                >{course.Course_name}</li>
                            ))}
                    </ul>

                    <button
                        className="m-3 btn btn-sm btn-danger"
                        onClick={this.removeAll}
                        >Remove All</button>
                </div>
                <div className="col-md-6">
                    {currentCourse ? (
                        <div>
                            <h4>Course</h4>
                            <div>
                                <label>
                                    <strong>Course ID:</strong>
                                </label>{" "}
                                {currentCourse.Course_id}
                            </div>
                            <div>
                                <label>
                                    <strong>Course Name</strong>
                                </label>{" "}
                                {currentCourse.Course_name}
                            </div>
                            <div>
                                <label>
                                    <strong>Course Term:</strong>
                                </label>{" "}
                                {currentCourse.Course_term}
                            </div>
                            <div>
                                <label>
                                    <strong>Course Year:</strong>
                                </label>{" "}
                                {currentCourse.Course_year}
                            </div>
                            <div>
                                <label>
                                    <strong>Professor ID:</strong>
                                </label>{" "}
                                {currentCourse.Prof_id}
                            </div>
                            <div>
                                <label>
                                    <strong>Course Start Time:</strong>
                                </label>{" "}
                                {currentCourse.Course_start}
                            </div>
                            <div>
                                <label>
                                    <strong>Course End Time:</strong>
                                </label>{" "}
                                {currentCourse.Course_end}
                            </div>
                            <div>
                                <label>
                                    <strong>Course Day(s):</strong>
                                </label>{" "}
                                {currentCourse.Course_day}
                            </div>
                        </div>
                    ) : (
                        <div>
                            <br/>
                            <p>Please click on a course.</p>
                        </div>
                    )}
                </div>
            </div>
        );
    }
}